@font-face {
  font-family: 'neon';
  src: url('fonts/neon.ttf')
}

.Background {
  position: fixed; top: 0; left: 0; right: 0; bottom: 0;
}

.App {
  text-align: center;

  background-color: rgba(0,0,0,0.0);

  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-around;

  position: fixed; top: 0; left: 0; right: 0; bottom: 0;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family:'neon', sans-serif;
  font-size: calc(250px + 2vmin);
}

.App-header b{
  font: 400 19vh "neon";
  background: -webkit-linear-gradient(#df5cea, #4004dc);
  color: #fee;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em #4004dc, 0 0 0.5em #4004dc, 0 0 0.1em #4004dc, 0 10px 3px #000;
}
.App-header b span{
  animation: blink linear infinite 2s;
}
.App-header b span:nth-of-type(2){
  animation: blink linear infinite 3s;
}
@keyframes blink {
  78% {
    color: inherit;
    text-shadow: inherit;
  }
  79%{
     color: #333;
  }
  80% {
    
    text-shadow: none;
  }
  81% {
    color: inherit;
    text-shadow: inherit;
  }
  82% {
    color: #333;
    text-shadow: none;
  }
  83% {
    color: inherit;
    text-shadow: inherit;
  }
  92% {
    color: #333;
    text-shadow: none;
  }
  92.5% {
    color: inherit;
    text-shadow: inherit;
  }
}
